@import url('https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300|Open+Sans:300,400,600,800|Oswald:300,400');
.primaryBold {
  font-family: 'Open Sans', sans-serif;
  font-size: 2rem;
  font-weight: 600;
  text-transform: uppercase;
}
.gridGeneral {
  position: relative;
  display: flex;
  overflow: hidden;
  box-sizing: border-box;
}
.sf-expand {
  flex-grow: 1;
}
.sf-container {
  position: relative;
  display: flex;
  overflow: hidden;
  box-sizing: border-box;
}
.sf-container-stack {
  position: relative;
  display: flex;
  overflow: hidden;
  box-sizing: border-box;
  flex-direction: column;
  flex-basis: 100%;
}
.white-bg {
  background: #fff !important;
}
.white {
  color: #fff;
}
@font-face {
  font-family: 'Century Gothic';
  src: url("../fonts/centuryGothic.ttf");
}
@font-face {
  font-family: 'Segoe';
  src: url("../fonts/SegoeUI.ttf");
}
.section-general {
  max-height: 100%;
  width: auto;
}
.hero-section {
  height: 350px;
  max-height: 100%;
  width: auto;
}
.section {
  max-height: 100%;
  width: auto;
}
.info-section {
  background: #343434;
}
.info-section {
  background: #343434;
  color: #FFF;
  text-align: center;
  padding-bottom: 3.8%;
  height: unset;
  max-height: unset;
}
.info-section p {
  margin: 0;
  margin-top: .75%;
}
.hero-title,
.info-section > .sub-title {
  font-family: 'Century Gothic';
  font-size: 2.4rem;
  text-transform: uppercase;
}
.info-section > .sub-text {
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
  font-size: 1rem;
}
div.info-button,
.info-section > div.info-button {
  margin-top: 1.3%;
  border: 1px solid #343434;
  font-family: 'Open Sans', sans-serif;
  text-transform: uppercase;
  color: #FFF;
  border: 1px solid #FFF;
  padding: 15px 32px;
  align-self: center;
  font-size: 1rem;
  font-weight: 300;
}
.more-info-section {
  background: #fff;
  margin: 6% 8% 2% 8%;
  justify-content: center;
  align-items: center;
}
.hero-section > img {
  height: 100%;
  max-height: auto;
  width: 100%;
}
.card-box {
  width: 65%;
  box-shadow: 1px 1px 1px 1px #cdcdcd;
  font-family: 'Open Sans';
  font-size: 1rem;
}
.card-box > p {
  text-align: center;
  padding: 1% 8%;
}
.card-box > img {
  justify-self: center;
  align-self: center;
}
.bg-info-section {
  height: 380px;
  background: url("../images/banner02.jpg");
  background-size: 100% 100%;
}
.bg-info-section1 .image1 {
  width: 50%;
}
.bg-info-section1 .image2 {
  width: 50%;
}
.hero2 {
  height: 350px;
  background: url("../images/coffee_read_newspaper.jpg");
  background-size: 100% 100%;
}
.center-center {
  justify-content: center;
  align-items: center;
}
body {
  box-sizing: border-box;
  max-width: 100vw;
  max-width: 1920px;
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  border: 1px solid red;
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 14px;
}
header {
  height: 15% !important;
  max-height: 15% !important;
}
#navigation {
  width: 100%;
  background: #fff;
}
#footer {
  background: #343434;
}
#main-content {
  flex-grow: 1;
}
#logo-container {
  display: flex;
  max-width: 18%;
  padding: 2% 8.33%;
}
#logo-container > img {
  width: 196px;
  height: 30px;
}
#menu-container {
  flex-grow: 1;
  color: #343434;
  display: flex;
}
#menu-container > ul {
  list-style: none;
  flex-grow: 1;
  margin: 0;
  justify-content: flex-end;
  align-items: center;
  margin-right: 2%;
}
#menu-container > ul > li {
  padding: 1%;
  text-transform: uppercase;
  font-size: .9rem;
  font-family: 'Open Sans', sans-serif;
  margin-right: 2%;
}
#login-container {
  padding-right: 8.33%;
  justify-content: center;
  align-items: center;
}
#login-container > span {
  display: block;
  font-family: 'Open Sans', sans-serif;
  padding: 10px 32px;
  border: 1px solid #343434;
}
